.card, .card .imgBx img, .card .details div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.card {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background: transparent;
  overflow: hidden; }
  .card:hover .imgBx {
    transform: scaleX(-1); }
  .card .imgBx,
  .card .details {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    transition: transform 0.3s; }
  .card .imgBx {
    top: 0;
    left: 0; }
    .card .imgBx img {
      width: 80%; }
      @media only screen and (max-width: 639px) {
        .card .imgBx img {
          height: 80%; } }
  .card .details {
    top: 0;
    left: 100%;
    padding: 20px 10px;
    background: #c3b6aa; }
    .card .details div {
      width: 90%; }
      .card .details div h5 {
        margin: 0 0 10px 0;
        padding: 0 0 10px;
        text-align: center;
        color: #ffffff;
        border-bottom: 2px solid #ffffff; }
        .card .details div h5 {
          font-size: 16px; }
        @media (min-width: 768px) and (max-width: 1024px) {
          .card .details div h5 {
            font-size: calc( 16px + 16 * (100vw - 768px) / 256); } }
        @media (min-width: 1024px) {
          .card .details div h5 {
            font-size: 32px; } }
        @media only screen and (max-width: 639px) {
          .card .details div h5 {
            font-size: 16px; } }

@media only screen and (max-width: 639px) and (min-width: 480px) and (max-width: 640px) {
  .card .details div h5 {
    font-size: calc( 16px + 12 * (100vw - 480px) / 160); } }

@media only screen and (max-width: 639px) and (min-width: 640px) {
  .card .details div h5 {
    font-size: 28px; } }
      .card .details div p {
        color: #ffffff; }
        .card .details div p {
          font-size: 12px; }
        @media (min-width: 768px) and (max-width: 1024px) {
          .card .details div p {
            font-size: calc( 12px + 12 * (100vw - 768px) / 256); } }
        @media (min-width: 1024px) {
          .card .details div p {
            font-size: 24px; } }
        @media only screen and (max-width: 639px) {
          .card .details div p {
            font-size: 12px; } }

@media only screen and (max-width: 639px) and (min-width: 480px) and (max-width: 640px) {
  .card .details div p {
    font-size: calc( 12px + 8 * (100vw - 480px) / 160); } }

@media only screen and (max-width: 639px) and (min-width: 640px) {
  .card .details div p {
    font-size: 20px; } }
