.avatar .frame img {
  position: absolute;
  left: -100%;
  right: -100%;
  top: -100%;
  bottom: -100%;
  margin: auto;
  width: 100%;
  height: auto; }
  .avatar .frame img:global(.landscape) {
    width: auto;
    height: 100%; }

.profile {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  min-width: 320px -20px;
  max-width: 640px;
  overflow: hidden; }
  @media only screen and (min-width: 1248px) {
    .profile {
      padding: 85px 20px 30px; } }
  @media only screen and (max-width: 1247px) {
    .profile {
      padding: 85px 20px 30px; } }
  @media only screen and (max-width: 767px) {
    .profile {
      padding: 80px 15px 15px; } }
  @media only screen and (max-width: 559px) {
    .profile {
      padding: 60px 10px 10px; } }
  @media only screen and (max-width: 399px) {
    .profile {
      padding: 50px 5px 5px; } }
  .profile .wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 480px;
    box-shadow: 3px 3px 5px rgba(255, 217, 159, 0.3);
    text-shadow: 3px 3px 3px rgba(255, 217, 159, 0.2); }

.avatar {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../../static/images/background/bg.jpg); }
  .avatar .frame {
    padding-top: 56.25%; }
    .avatar .frame img {
      width: auto;
      max-height: 150%;
      transition-duration: 0.5s; }
  .avatar:hover .frame img {
    transform: translateY(-2.5%) scale(1.1); }
  .avatar::after, .avatar::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    border-style: solid;
    border-color: #ffffff; }
  .avatar::before {
    border-width: 0.5rem 0 0 0.5rem; }
  .avatar::after {
    border-width: 0 0.5rem 0.5rem 0; }

.info {
  position: relative;
  margin-top: -1px;
  padding: 0.5rem 1rem 1rem;
  text-align: center;
  background-color: #ffffff; }
  .info h2 {
    margin: 0;
    font-weight: bold;
    letter-spacing: 1px; }
  .info p {
    margin: 10px;
    text-align: justify;
    text-justify: inter-ideograph; }
