.singleChart .text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.singleChart {
  position: relative;
  transition: transform 0.3s; }
  .singleChart:hover, .singleChart:active {
    transform: scale(1.1); }
    .singleChart:hover .circle, .singleChart:active .circle {
      stroke: #e23939;
      stroke-width: 2;
      stroke-dasharray: 100; }
  .singleChart .circularChart {
    margin: 10px auto;
    display: block;
    max-width: 60%; }
    @media only screen and (max-width: 559px) {
      .singleChart .circularChart {
        max-width: 80%; } }
  .singleChart .circleBg {
    fill: none;
    stroke: #f4f4f4;
    stroke-width: 3.8; }
  .singleChart .circle {
    fill: none;
    stroke: #ec5c1e;
    stroke-width: 2.8; }
  .singleChart .text {
    user-select: none; }
    .singleChart .text {
      font-size: 24px; }
    @media (min-width: 320px) and (max-width: 1024px) {
      .singleChart .text {
        font-size: calc( 24px + 4 * (100vw - 320px) / 704); } }
    @media (min-width: 1024px) {
      .singleChart .text {
        font-size: 28px; } }
  .singleChart circle {
    fill: #ffc56c; }
  .singleChart._AI circle {
    fill: #3b3323; }
  .singleChart._AI .circle {
    stroke: #fc9d0e; }
  .singleChart._AI .text {
    color: #fc9d0e; }
  .singleChart._AE circle {
    fill: #312963; }
  .singleChart._AE .circle {
    stroke: #c88fff; }
  .singleChart._AE .text {
    color: #c88fff; }
  .singleChart._PR circle {
    fill: #3a2641; }
  .singleChart._PR .circle {
    stroke: #d757ff; }
  .singleChart._PR .text {
    color: #d757ff; }
  .singleChart._PS circle {
    fill: #2d2668; }
  .singleChart._PS .circle {
    stroke: #90c5fc; }
  .singleChart._PS .text {
    color: #90c5fc; }
  .singleChart._3D circle {
    fill: #2f979f; }
  .singleChart._3D .circle {
    stroke: #2c2e78; }
  .singleChart._3D .text {
    color: #2c2e78; }

:global(.aos-animate) > .singleChart .circle {
  animation-name: progress;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-delay: 0.1s;
  animation-fill-mode: forwards; }

@keyframes progress {
  0% {
    stroke-dasharray: 0 100; } }
