.block {
  position: relative;
  padding: 0 1rem 2rem; }
  .block .title {
    position: relative;
    margin-bottom: 2rem;
    padding: 0 10px 10px;
    color: #ec5c1e;
    border-radius: 5px; }
    .block .title::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 5px;
      background: #ec5c1e; }
